.footer {
  &__container {
    margin-bottom: 43px;
    @media screen and (min-width: 768px) {
      margin-bottom: px;
      display: flex;
    }
    @media screen and (min-width: 1280px) {
      margin-bottom: 60px;
    }
  }

  position: relative;
  background-color: #fff5f6;
  min-width: 320px;

  &__text-box {
    width: 232px;

    @media screen and (min-width: 768px) {
      width: 340px;
    }
    @media screen and (min-width: 1280px) {
      width: 480px;

      &:hover {
        .footer__title--made {
          color: #d41443;
          transform: translate(-170px, 27px);
        }
        .footer__title--ice-cream {
          transform: translate(55px, 0px);
        }
      }
    }
  }

  &__title {
    cursor: default;
    text-align: left;
    font-family: 'Titan One', sans-serif;
    font-weight: 400;
    text-transform: uppercase;
    @extend %anim-all;
    &--one {
      font-size: 18px;
      line-height: 1.167;
      color: #ffa5ba;
      margin-bottom: 17px;

      @media screen and (min-width: 768px) {
        margin-top: 7px;
        margin-bottom: 16px;
        font-size: 26px;
        line-height: 1.154;
        letter-spacing: 0.04em;
      }
      @media screen and (min-width: 1280px) {
        margin-top: 0px;
        margin-bottom: 15px;
        font-size: 30px;
        line-height: 1.133;
      }
    }

    &--two {
      font-size: 34px;
      line-height: 1.147;
      letter-spacing: 0.04em;
      color: #d41443;
      margin-bottom: 43px;
      display: flex;
      flex-direction: column;

      @media screen and (min-width: 768px) {
        font-size: 48px;
        line-height: 1.146;
        letter-spacing: 0.06em;
        margin-bottom: 0px;
      }
      @media screen and (min-width: 1280px) {
        font-size: 58px;
        line-height: 1.138;
        letter-spacing: 0.06em;
        flex-direction: row;
      }
    }

    &--word {
      display: flex;
    }

    &--letter {
      @media screen and (min-width: 768px) {
        &:hover {
          transform: scale(1.2);
        }
      }

      &-spase {
        @media screen and (min-width: 1280px) {
          margin-right: 17px;
        }
      }
      &-push {
        margin-right: -2px;
      }
    }
    &--made,
    &--ice-cream,
    &--letter {
      display: inline-block;
      @extend %anim-all;
    }
  }
}

.social-box {
  margin-bottom: 46px;
  @media screen and (min-width: 1280px) {
    margin-bottom: 50px;
    margin-right: 5px;
  }

  &__list {
    &:not(:first-child) {
      margin-left: 28px;
      @media screen and (min-width: 768px) {
        margin-left: 30px;
      }
      @media screen and (min-width: 1280px) {
        margin-left: 25px;
      }
    }
  }
  &__links {
    display: flex;
    justify-content: flex-end;
  }
  &__icon {
    display: block;
    fill: #ffa5ba;
    @extend %anim-fill;

    .social-box__link:hover &,
    .social-box__link:focus & {
      fill: #d41443;
    }
  }
}

.contact-box {
  font-weight: 700;
  text-align: right;
  font-size: 12px;
  line-height: 1.86;
  cursor: default;

  @media screen and (min-width: 768px) {
    font-size: 14px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
    letter-spacing: 0.02em;
  }

  &__phone {
    display: inline-block;
    color: #d41443;
    @extend %anim-transform;
    &:focus,
    .contact-box:hover & {
      transform: scale(1.2) translateX(12px);
    }
  }
}

.copyright-box {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: default;
  border-top: 1px solid #e1e1e1;
  padding-top: 22px;
  padding-bottom: 25px;

  @media screen and (min-width: 768px) {
    padding-top: 29px;
    padding-bottom: 18px;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 9px;
    padding-bottom: 10px;
  }

  &__text {
    color: #907e82;
    font-weight: 700;
    text-align: center;
    max-width: 150px;
    font-size: 14px;
    line-height: 1.52;
    @extend %anim-transform;

    @media screen and (min-width: 768px) {
      line-height: 1.86;
      max-width: 276px;
      letter-spacing: 0.02em;
    }

    &:hover {
      transform: scale(1.3);
    }
  }
}

@media screen and (min-width: 768px) {
  .contacts-box {
    margin-left: auto;
  }
}
/*Animation variables*/

$anim-func: cubic-bezier(0.4, 0, 0.2, 1);
$anim-time: 500ms;

/*Animation placeholders*/

%anim-all {
  transition: all 500ms cubic-bezier(0.4, 0, 0.2, 1);
}

%anim-transform {
  transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1),
    color 500ms cubic-bezier(0.4, 0, 0.2, 1);
}

%anim-fill {
  transition: fill 500ms cubic-bezier(0.4, 0, 0.2, 1);
}
